<template>
  <div class="categorys">
    <!-- 侧边栏 -->
    <van-sidebar v-model="activeKey" @change="getProductList">
      <van-sidebar-item
        v-for="category in categorys"
        :key="category.block_id"
        :title="category.category_name"
      />
    </van-sidebar>
    <!-- 主题内容 -->
    <div class="mainContent">
      <template v-for="(item, index) in productList">
        <CellsAutoFill
          :key="index"
          v-if="item.view_type === 'cells_auto_fill'"
          :item="item"
        />

        <CategoryGroupOne
          :key="item.category_id"
          v-if="item.view_type === 'category_group'"
          :group="item"
        />

        <CategoryGroup
          :key="index"
          v-if="item.view_type === 'category_group'"
          :item="item"
        />

        <CategoryTitle
          :key="index"
          v-if="item.view_type === 'category_title'"
          :item="item"
        />
      </template>
    </div>
  </div>
</template>

<script>
import CategoryGroup from "@/components/category/CategoryGroup.vue";
import CategoryGroupOne from "@/components/category/CategoryGroupOne.vue";
import CategoryTitle from "@/components/category/CategoryTitle.vue";
import CellsAutoFill from "@/components/category/CellsAutoFill.vue";

export default {
  components: {
    CellsAutoFill,
    CategoryTitle,
    CategoryGroup,
    CategoryGroupOne,
  },
  data() {
    return {
      // 控制侧边栏活动的item
      activeKey: 0,
      categorys: null,
      productList: null,
    };
  },
  methods: {
    getCategory() {
      this.axios.get("home/category_v2").then((res) => {
        console.log("getCategory", res.data.data);
        this.categorys = res.data.data;
      });
    },
    getProductList(index) {
      if (!this.categorys) {
        this.axios.get(`home/category_v2?cat_id=-1`).then((res) => {
          console.log(res.data.data[0].category_list);
          this.productList = res.data.data[0].category_list;
        });
      } else {
        this.axios
          .get(`home/category_v2?cat_id=${this.categorys[index]?.category_id}`)
          .then((res) => {
            console.log(res.data.data[0].category_list);
            this.productList = res.data.data[0].category_list;
          });
      }
    },
  },
  created() {
    this.getCategory();
    this.getProductList();
  },
};
</script>

<style lang="less" scoped>
.categorys {
  height: 79vh;
  overflow: scroll;
  display: flex;

  .van-sidebar-item {
    font-size: 12px;
    white-space: nowrap;
    padding: 20px 4px;
    text-align: center;
  }
  .mainContent {
    height: 100%;
    width: 76vw;
    margin-left: 5px;
    overflow: scroll;
  }
}
</style>
