<template>
  <ul>
    <li v-for="lis in item.body.items" :key="lis.block_id" @click="xxx(lis.action.type,lis.action.path)">
      <img :src="lis.img_url" alt="" />
      <h4 class="name text-ellipsis">{{ lis.product_name }}</h4>
    </li>
  </ul>
</template>

<script>
export default {
  props: ["item"],
  methods:{
    xxx(type,id){
      //  || type === "keyword"
      if(type ==="cate"){
        this.$router.push({path:'/categorylist',query:{ id:id}})
      }else if(type === "product"){
        this.$router.push({ path: '/productdetails', query: {product_id:id} })
      }
    }
  }
};
</script>

<style lang="less" scoped>
ul {
  display: flex;
  // justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  li {
    width: 20vw;
    margin: 0 10px;
    img {
      width: 100%;
    }
    .name {
      margin: 8px;
      font-size: 12px;
    }
    .text-ellipsis {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      padding: 4px;
      margin: 0;
    }
  }
}
</style>